<template>
    <div id="search">
        <div class="top">
            <div class="search">
                <router-link to="/search">
                    <img src="https://vm.gtimg.cn/tencentvideo/vstyle/mobile/v2/style/img/common/search_logo.png" alt="">
                </router-link>
                <input type="text" placeholder="" @keydown.enter="add_history()">
                <router-link to="/index">
                    <span></span>
                </router-link>
                <svg class="icon" aria-hidden="true" @click="clear_input()">
                    <use xlink:href="#icon-guanbi"></use>
                </svg>
            </div>
            <div class="search_history" v-if="search_history.length!=0">
                <div class="his_top">
                    <span></span>
                    <svg class="icon" aria-hidden="true" @click="clear_history()">
                        <use xlink:href="#icon-shanchu"></use>
                    </svg>
                </div>
                <div class="his_bottom">
                    <p v-for="(item,index) in search_history">{{item}}</p>
                </div>
            </div>
            <div class="nav">
                <ul>
                    <li v-for="(item,index) in nav" :class="{current_li:current_index==index}" @click="change(index)">
                        {{item.nav_name}}
                    </li>
                </ul>
            </div>
            <div class="range">
                <ul>
                    <li v-for="(item,index) in nav[current_index].list">
                        <span>{{index+1}}</span>
                        <span>{{item.video_name}}</span>
                        <svg class="icon" aria-hidden="true">
                            <use :xlink:href="item.svg_href"></use>
                        </svg>
                    </li>
                </ul>
            </div>
            <div class="footer">
                <p>Copyright  1998 - 2020 Tencent. All Rights Reserved</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            current_index:0,
            search_history:[],
            nav:[
                {
                    nav_name:'',
                    list:[
                    {
                        video_name:'',
                        svg_href:'#icon-chiping'
                    },
                    {
                        video_name:' 4',
                        svg_href:'#icon-chiping'
                    },
                    {
                        video_name:' 3',
                        svg_href:'#icon-shangsheng'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-chiping'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-xiajiang'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-chiping'
                    },
                    {
                        video_name:' 6',
                        svg_href:'#icon-xiajiang'
                    },
                    {
                        video_name:' 5',
                        svg_href:'#icon-shangsheng'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-shangsheng'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-xiajiang'
                    },
                    {
                        video_name:' 2',
                        svg_href:'#icon-xiajiang'
                    },
                    {
                        video_name:'2020',
                        svg_href:'#icon-xiajiang'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-xiajiang'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-shangsheng'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-shangsheng'
                    },
                    ]
                },
                {
                    nav_name:'',
                    list:[
                    {
                        video_name:'',
                        svg_href:'#icon-chiping'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-chiping'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-xiajiang'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-chiping'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-chiping'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-shangsheng'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-xiajiang'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-chiping'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-shangsheng'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-xiajiang'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-shangsheng'
                    },
                    {
                        video_name:'2',
                        svg_href:'#icon-xiajiang'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-chiping'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-xiajiang'
                    },
                    {
                        video_name:'',
                        svg_href:'#icon-shangsheng'
                    },
                    ]
                },
                {
                    nav_name:'',
                    list:[]
                },
                {
                    nav_name:'',
                    list:[]
                },
                {
                    nav_name:'',
                    list:[]
                },
                {
                    nav_name:'',
                    list:[]
                },
                {
                    nav_name:'',
                    list:[]
                },
                {
                    nav_name:'',
                    list:[]
                },
                {
                    nav_name:'',
                    list:[]
                },
                {
                    nav_name:'',
                    list:[]
                },
                {
                    nav_name:'',
                    list:[]
                },
            ]
        }
    },
    methods:{
        change(temp){
            this.current_index=temp;
        },
        add_history(){
            this.search_history.unshift($('input').eq(0).val());
            $('input').eq(0).val('');
        },
        clear_input(){
            if($('input').eq(0).val()!=''){
                $('input').eq(0).attr('placeholder','');
            }
            $('input').eq(0).val('');
        },
        clear_history(){
            this.search_history=[];
        }
    }
}
</script>

<style scoped>
    #search{
        margin: 0px -13px;
    }
    #search .top .search{
        height: 35px;
        padding: 10px 10px;
        position: relative;
    }
    #search .top .search img{
        width: 75px;
    }
    #search .top .search input{
        width: calc(100% - 146px);
        height: 33px;
        font-size: 14px;
        line-height: 16px;
        position: relative;
        top: -7px;
        padding: 0px 13px;
        border: 1px solid skyblue;
        border-radius: 18px;
        outline: none;
    }
    #search .top .search span{
        display: inline-block;
        width: 28px;
        color: #383838;
        position: relative;
        top: -7px;
        right: 2px;
        font-size: 14px;
        line-height: 33px;
        margin-left: 5px;
    }
    #search .top .search svg{
        position: absolute;
        font-size: 20px;
        top: 17px;
        right: 55px;
    }
    #search .search_history .his_top{
        padding: 10px 10px 5px;
        margin: 0px 6px 5px;
        color: #8d8d8d;
        font-size: 13px;
    }
    #search .search_history .his_top svg{
        float: right;
        font-size: 15px;
    }
    #search .search_history .his_bottom{
        max-height: 74px;
        padding-left: 10px;
        overflow: hidden;
    }
    #search .search_history .his_bottom p{
        display: inline-block;
        max-width: calc(100% - 10px);
        margin: 0px 10px 10px 0px;
        padding: 0px 10px;
        overflow: hidden;
        border-radius: 2px;
        background-color: #f7f7f7;
        color: #4c4c4c;
        font-size: 14px;
        line-height: 30px;
        /* -webkit-line-clamp: 1; */
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    #search .nav{
        width: 100%;
        overflow: auto;
    }
    #search .nav::-webkit-scrollbar{
        display: none;
    }
    #search .nav ul{
        width: 498px;
        padding: 6px 8px;
        overflow: auto;
    }
    #search .nav li{
        float: left;
        font-size: 14px;
        color: #222;
        margin: 0px 8px 5px;
    }
    #search .nav .current_li{
        color: #ff6022;
        font-weight: bold;
    }
    #search .nav .current_li:after{
        content:'';
        display: block;
        width: 18px;
        height: 2.5px;
        background: #ff6022;
        margin: 3px auto 0px;
    }
    #search .range{
        padding-left: 3px;
    }
    #search .range ul{
        padding-left: 10px;
    }
    #search .range li{
        margin-top: 10px;
        padding-bottom: 10px;
        border-bottom: 1px solid #ebebeb;
        position: relative;
    }
    #search .range li span:nth-of-type(1){
        display: inline-block;
        font-size: 14px;
        width: 16px;
        height: 16px;
        line-height: 16px;
        text-align: center;
        margin-right: 5px;
        background: #e6e6e6;
        color: #878787;
    }
    #search .range li:nth-of-type(1) span:nth-of-type(1){
        background: #e42818;
        color: #fff;
    }
    #search .range li:nth-of-type(2) span:nth-of-type(1){
        background: #ff7100;
        color: #fff;
    }
    #search .range li:nth-of-type(3) span:nth-of-type(1){
        background: #ffb900;
        color: #fff;
    }
    #search .range li span:nth-of-type(2){
        color: #4c4c4c;
        font-size: 14px;
        line-height: 30px
    }
    #search .range li svg{
        font-size: 10px;
        position: absolute;
        right: 10px;
        top: 10px;
    }
    #search .footer{
        height: 48px;
        width: 100%;
        line-height: 48px;
        text-align: center;
        font-size: 10px;
        color: #b3b3b3;
        background-color: #eaeaea;
    }
</style>


